import React, { useState } from 'react';
import './App.css';
import itemsArray from './components/Items.data';
import { ItemsDivider } from "./components/Items";
import { CartDivider } from "./components/Cart";
import { CheckoutForm, ConfirmationDivider } from "./components/Checkout";

function App() {

  const [page, setPage] = useState("products");
  const [items, setItems] = useState(itemsArray);
  const [customer, setCustomer] = useState({ firstname: "Maija", lastname: "Meikäläinen", email: "mm@gmail.com", phone: "123 4567 789", address: "Koulukatu 11B", postnum: "33100", postoffice: "Tampere" });

  return (
    <div className="App">
      <header className="App-header">
        <h3>Koirantarvike-kauppa</h3>
      </header>
      {page === "products" &&
      <div className="shop">
        <button className="topButton" onClick={() => { setPage("cart")}}>{"Näytä ostoskori!"}</button>
        <ItemsDivider items={items} setItems={setItems} />
      </div>
      }
      {page === "checkout" &&
        <div className="checkoutParent">
          <button className="topButton" onClick={() => { setPage("cart") }}>{"Näytä ostoskori!"}</button>
          <CheckoutForm setCustomer={setCustomer} setPage={setPage} items={items} setItems={setItems} />
        </div>
      }
      {page === "cart" &&
        <div className="cartParent">
          <button className="topButton" onClick={() => { setPage("products") }}>{"Näytä tuotteet!"}</button>
          <CartDivider items={items} setItems={setItems} setPage={setPage} />
        </div>
      }
      {page === "confirmation" &&
        <div className="confirmationParent">
          <ConfirmationDivider customer={customer} setPage={setPage} items={items} setItems={setItems} />
        </div>
      }
    </div>
  );
}

export default App;
