import React from 'react';

const CartDiv = ({ item, addItem, removeItem, removeAll }) => {

    return (
        <tr className="tr-cart">
            <td>
                <img className="cart-img" src={item.img} alt={item.name} />
            </td>
            <td>
                <h4>{item.name}</h4>
            </td>
            <td>
                <p>Hinta yht. {item.price * item.amount}€</p>
            </td>
            <td>
                <p>Määrä: {item.amount}</p>
            </td>
            <td>
                <button onClick={() => addItem(item.id)}>+</button>
            </td>
            <td>
                <button onClick={() => removeItem(item.id)}>-</button>
            </td>
            <td>
                <button onClick={() => removeAll(item.id)}>Poista kaikki</button>
            </td>
        </tr>
    )
}

const CartDivider = ({ items, setItems, setPage }) => {
    const addItem = (id) => {
        const tempAmounts = items.map(item => {
            if (item.id === id) {
                const tempItem = { ...item, amount: item.amount + 1 }
                return tempItem;
            } else {
                return item;
            }
        })
        setItems(tempAmounts);
    }

    const removeItem = (id) => {
        const tempAmounts = items.map(item => {
            if (item.id === id) {
                const tempItem = { ...item, amount: item.amount - 1 }
                return tempItem;
            } else {
                return item;
            }
        })
        setItems(tempAmounts);
    }

    const removeAll = (id) => {
        const tempAmounts = items.map(item => {
            if (item.id === id) {
                const tempItem = { ...item, amount: item.amount = 0 }
                return tempItem;
            } else {
                return item;
            }
        })
        setItems(tempAmounts);
    }

    const emptyCart = () => {
        const tempAmounts = items.map(item => {
            const tempItem = { ...item, amount: item.amount = 0 }
            return tempItem;
        })
        setItems(tempAmounts);
    }

    const _cartTotal = () => {
        const tempTotal = items.map(item => item.price * item.amount).reduce((a, b) => a + b, 0);
        if (tempTotal >= 100 && tempTotal < 250) {
            const cartTotal = (tempTotal * 0.975).toFixed(2) + "€, alennus 2.5%";
            return cartTotal;
        } else if (tempTotal >= 250 && tempTotal < 500) {
            const cartTotal = (tempTotal * 0.96).toFixed(2) + "€, alennus 4%";
            return cartTotal;
        } else if (tempTotal >= 500) {
            const cartTotal = (tempTotal * 0.9).toFixed(2) + "€, alennus 10%";
            return cartTotal;
        }
        else {
            return (tempTotal.toFixed(2))
        }
    }

    const itemsInCart = items.filter(item => item.amount > 0);

    let button0;
    if (itemsInCart.length > 0) {
        button0 = <button onClick={() => emptyCart()}>Tyhjennä ostoskori</button>;
    } else {
        button0 = <button disabled onClick={() => emptyCart()}>Tyhjennä ostoskori</button>;
    }

    let button1;
    if (itemsInCart.length > 0) {
        button1 = <button className="confirm" onClick={() => { setPage("checkout") }}>{"Vahvista tilaus"}</button>;
    } else {
        button1 = <button className="topButton" disabled onClick={() => { setPage("checkout") }}>{"Vahvista tilaus"}</button>;
    }

    return (
        <div className="cart">
            <h3>Ostoskori</h3>
            <table className="table-cart">
                <tbody>
                {itemsInCart.map(item => <CartDiv key={item.id} item={item} addItem={addItem} removeItem={removeItem} removeAll={removeAll} />)}
                </tbody>
            </table>
            {button0}
            <h3>Yhteensä: {_cartTotal()}€</h3>
            {button1}
        </div>
    )
}

export { CartDiv, CartDivider };