import React, { useState } from 'react';

const CheckoutForm = ({ setCustomer, setPage }) => {
    const [newFirstname, setNewFirstname] = useState("");
    const [newLastname, setNewLastname] = useState("");
    const [newEmail, setNewEmail] = useState("");
    const [newPhone, setNewPhone] = useState("");
    const [newAddress, setNewAddress] = useState("");
    const [newPostnum, setNewPostnum] = useState("");
    const [newPostOffice, setNewPostOffice] = useState("");

    const handleSubmitCustomer = (e) => {
        e.preventDefault();
        setCustomer({
            firstname: newFirstname,
            lastname: newLastname,
            email: newEmail,
            phone: newPhone,
            address: newAddress,
            postnum: newPostnum,
            postoffice: newPostOffice
        });
        setNewFirstname("");
        setNewLastname("");
        setNewEmail("");
        setNewPhone("");
        setNewAddress("");
        setNewPostnum("");
        setNewPostOffice("");
        setPage("confirmation");
    }

    return (
        <div className="checkout">
            <form onSubmit={handleSubmitCustomer}>
                <label for="firstname">Etunimi:</label><br/>
                <input 
                    onChange={e => setNewFirstname(e.target.value)} type="text" id="firstname" name="firstname" placeholder="Etunimi" value={newFirstname} required 
                /><br/>
                <label for="lastname">Sukunimi:</label><br />
                <input 
                    onChange={e => setNewLastname(e.target.value)} type="text" id="lastname" name="lastname" placeholder="Sukunimi" value={newLastname} required 
                /><br />
                <label for="email">Sähköposti:</label><br />
                <input 
                    onChange={e => setNewEmail(e.target.value)} type="email" id="email" name="email" placeholder="Sähköposti" value={newEmail} required 
                /><br />
                <label for="phone">Puhelin:</label><br />
                <input 
                    onChange={e => setNewPhone(e.target.value)} type="number" id="phone" name="phone" placeholder="Puhelinnumero" value={newPhone} required 
                /><br />
                <label for="address">Osoite:</label><br />
                <input 
                    onChange={e => setNewAddress(e.target.value)} type="text" id="address" name="address" placeholder="Osoite" value={newAddress} required 
                /><br />
                <label for="postnum">Postinumero:</label><br />
                <input
                    onChange={e => setNewPostnum(e.target.value)} type="number" id="postnum" name="postnum" placeholder="Postinumero" value={newPostnum} required
                /><br />
                <label for="postOffice">Osoite:</label><br />
                <input
                    onChange={e => setNewPostOffice(e.target.value)} type="text" id="postOffice" name="postOffice" placeholder="Postitoimipaikka" value={newPostOffice} required
                /><br />
                <input type="submit" className="confirm" value="Vahvista"/>
            </form>
        </div>
    )
}

const ConfirmationCartDiv = ({ item }) => {
    return (
        <tr className="tr-cart">
            <td>
                <img className="cart-img" src={item.img} alt={item.name} />
            </td>
            <td>
                <h4>{item.name}</h4>
            </td>
            <td>
                <p>Hinta yht: {item.price * item.amount}€</p>
            </td>
            <td>
                <p>Määrä: {item.amount}</p>
            </td>
        </tr>
    )
}

const ConfirmationDivider = ({ customer, setPage, items, setItems}) => {

    const emptyCart = () => {
        const tempAmounts = items.map(item => {
            const tempItem = { ...item, amount: item.amount = 0 }
            return tempItem;
        })
        setItems(tempAmounts);
    }

    const itemsInCart = items.filter(item => item.amount > 0);

    return (
        <div className="confirmation">
            <h2>Kiitos tilauksesta!</h2>
            <h3>Tilaamasi tuotteet:</h3>
            <table className="table-cart">
                {itemsInCart.map(item => <ConfirmationCartDiv key={item.id} item={item} />)}
            </table>
            <div className="customerDiv">
                <h5>Nimi: {customer.firstname} {customer.firstname}</h5>
                <h5>Sähköposti: {customer.email}</h5>
                <h5>Puhelinnumero: {customer.phone}</h5>
                <h5>Osoite: {customer.address}, {customer.postnum} {customer.postoffice}</h5>
            </div>
            <button className="topButton" onClick={() => { setPage("products"); emptyCart(); }}>{"OK!"}</button>
        </div>
    )
}

export { CheckoutForm, ConfirmationDivider };