import React from 'react';

const ItemDiv = ({ item, addToCart }) => {

    const divid = "div" + item.id;

    return (
        <div className={divid}>
            <div className="product-top">
                <div className="product-textdiv">
                    <p>{item.name}</p>
                </div>
                <div className="product-imgdiv">
                    <img className="product-img" src={item.img} alt={item.name} />
                </div>
            </div>
            <div className="product-bottom">
                <p>Hinta: {item.price}€</p>
                <button onClick={() => addToCart(item.id)}>Lisää ostoskoriin</button>
            </div>
        </div>
    )

}

const ItemsDivider = ({ items, setItems }) => {

    if (items.itemsArray) {

        const addToCart = (id) => {
            const tempAmounts = items.itemsArray.map(item => {
                if (item.id === id) {
                    const tempItem = { ...item, amount: item.amount + 1 }
                    return tempItem;
                } else {
                    return item;
                }
            })
            setItems(tempAmounts);
        }

        return (
            <div className="gridParent">
                {items.itemsArray.map(item => <ItemDiv key={item.id} item={item} addToCart={addToCart} />)}
            </div>
        )

    } else {

        const addToCart = (id) => {
            const tempAmounts = items.map(item => {
                if (item.id === id) {
                    const tempItem = { ...item, amount: item.amount + 1 }
                    return tempItem;
                } else {
                    return item;
                }
            })
            setItems(tempAmounts);
        }

        return (
            <div className="gridParent">
                {items.map(item => <ItemDiv key={item.id} item={item} addToCart={addToCart} />)}
            </div>
        )

    }

}

export { ItemDiv, ItemsDivider };